import React from "react";
import { Link } from "react-router-dom";
import "./Header.css";

function Header() {
  return (
    <div className="stick">
      <div className="headerTitle">
        <div className="virticalText">The</div>
        <span className="titleHead">
          <Link to="./" className="headLinkText">
            Serin
          </Link>
        </span>
      </div>
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <div className="container-fluid">
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0 mx-2">
              <li className="nav-item mx-2">
                <Link className="nav-link active" aria-current="page" to="">
                  Home
                </Link>
              </li>
              <li className="nav-item mx-2">
                <Link className="nav-link" to="category/Bollywood">
                  Bollywood
                </Link>
              </li>
              <li className="nav-item mx-2">
                <Link className="nav-link" to="category/Technology">
                  Technology
                </Link>
              </li>
              <li className="nav-item mx-2">
                <Link className="nav-link" to="category/Sports">
                  Sports
                </Link>
              </li>
              <li className="nav-item mx-2">
                <Link className="nav-link" to="category/Health">
                  Health
                </Link>
              </li>
              <li className="nav-item mx-2">
                <Link className="nav-link" to="category/Business">
                  Business
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
}

export default Header;
